.form {
  width: 550px;
  flex-direction: column;
  margin: auto;
  padding: 50px;
  display: flex;
}

.form > input {
  height: 40px;
  border: 1px solid #aeaeae;
  border-radius: 10px;
  padding: 10px;
}

.form > label {
  margin: 10px 0 0;
}

.form > button {
  height: 40px;
